import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { AlertInformationDataHelisaComponent} from './alert-information-data-helisa.component';
import { AlertInformationType } from './alert-information-helisa-type.enum';

@Injectable({
  providedIn: 'root'
})
export class AlertInformationDataHelisaService {

  constructor(public dialog: MatDialog) { }

  openDialog(alertType: AlertInformationType, title?: string, content?: string, okLabel?: string, cancelLabel?: string): Observable<boolean> {
    const dialogRef: MatDialogRef<AlertInformationDataHelisaComponent> = this.dialog.open(AlertInformationDataHelisaComponent, {
      width: '250px',
      data: { alertType, title, content, okLabel, cancelLabel }
    });

    return dialogRef.afterClosed();
  }
}
