import { AfterViewInit, Component, ContentChild, EventEmitter, Input, OnInit, Output, TemplateRef } from '@angular/core';
import { Observable } from 'rxjs';

export enum PagingTreeInitialMode {
  COLLAPSE,
  EXPAND
}

export interface PagingTreeHelisaListable<T> {
  get(lastChildOrder: number, size: number): Observable<T[]>;

  getIdField(): string;

  getIdParentField(): string;

  compare(a: T, b: T): number;
}

interface HelisaNode<T> {
  object: T;
  level: number;
  haveChildren: boolean;
  expanded: boolean;
  visible: boolean;
  preorder: number;
}

export interface HelisaNodeData<T> {
  readonly object: T;
  readonly level: number;
  readonly haveChildren: boolean;
  readonly expanded: boolean;
  readonly visible: boolean;
  readonly preorder: number;
}

@Component({
  selector: 'hel-paging-tree',
  templateUrl: './paging-tree-helisa.component.html',
  styleUrls: ['./paging-tree-helisa.component.css']
})
export class PagingTreeHelisaComponent<T> implements OnInit, AfterViewInit {

  private pageSize: number = 200000;
  private visibleLimit: number = 0;
  private visibleSize: number = 100;
  private treeMode: PagingTreeInitialMode = PagingTreeInitialMode.EXPAND;
  private visibleObjects: Array<T> = [];
  private service: PagingTreeHelisaListable<T>;
  private searchNode: Map<string, HelisaNode<T>>;
  private allNode: Array<HelisaNode<T>> = [];

  @Output()
  afterLoadData: EventEmitter<void> = new EventEmitter<void>();

  @ContentChild('nodeComponent')
  nodeComponent: TemplateRef<{ data: T, node: HelisaNodeData<T> }>;

  @ContentChild('nodeTitle')
  nodeTitle: TemplateRef<any>;

  constructor() {
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
  }

  @Input()
  set mode(paramMode: PagingTreeInitialMode) {
    this.treeMode = paramMode;
    this.reset();
  }

  @Input()
  set pagingTreeHelisaListable(paramService: PagingTreeHelisaListable<T>) {
    this.service = paramService;
    this.reset();
  }

  public reset(): void {
    if (this.service) {
      this.service.get(0, this.pageSize).subscribe((items: T[]): void => this.loadData(items));
    }
  }

  private loadData(items: T[]): void {
    this.searchNode = new Map<string, HelisaNode<T>>();
    this.visibleObjects = [];
    this.allNode = [];
    items = this.sortItems(items);
    this.searchNode = new Map<string, HelisaNode<T>>();
    items.forEach((item: T): void => {
      const node: HelisaNode<T> = this.createNode(item);
      this.allNode.push(node);
    });
    this.reSort();
    this.loadNextVisibleObjects(null);
    this.afterLoadData.emit();
  }

  private sortItems(items: T[]): T[] {
    const lAdy: Map<string, T[]> = new Map<string, T[]>();
    const stack: T[] = [];
    items.forEach((item: T): void => {
      const idParent: string = item[this.service.getIdParentField()];
      if (!idParent) {
        stack.unshift(item);
      } else {
        if (!lAdy.has(idParent)) {
          lAdy.set(idParent, []);
        }
        lAdy.get(idParent).push(item);
      }
    });
    const response: T[] = new Array<T>(items.length);
    let index: number = 0;
    while (stack.length > 0) {
      const last: T = stack.pop();
      response[index++] = last;
      const children: T[] = lAdy.get(last[this.service.getIdField()]);
      if (children) {
        for (let i: number = children.length - 1; i >= 0; i--) {
          stack.push(children[i]);
        }
      }
    }
    return response;
  }

  private createNode(item: T): HelisaNode<T> {
    if (this.searchNode.has(item[this.service.getIdField()])) {
      throw Error('Ya existe el nodo.');
    }
    const parentInformation: HelisaNode<T> = this.getNodeInformationById(item[this.service.getIdParentField()]);
    const nodeInformation: HelisaNode<T> = {
      object: item,
      haveChildren: false,
      level: parentInformation ? parentInformation.level + 1 : 0,
      expanded: this.treeMode === PagingTreeInitialMode.EXPAND,
      visible: false,
      preorder: this.searchNode.size + 1,
    };
    this.searchNode.set(item[this.service.getIdField()], nodeInformation);
    if (parentInformation) {
      parentInformation.haveChildren = true;
    }
    return nodeInformation;
  }

  public getNodeInformationById(id: string): HelisaNode<T> {
    return this.searchNode.get(id);
  }

  public getNodeInformation(item: T): HelisaNode<T> {
    return this.searchNode.get(item[this.service.getIdField()]);
  }

  getLevelClass(item: T): string {
    return 'padding-level-' + this.getNodeInformationById(item[this.service.getIdField()]).level;
  }

  private loadNextVisibleObjects(nodeFrom: T): void {
    const visibleObjects: T[] = [];
    this.visibleObjects.forEach((item: T): void => {
      if (this.getNodeInformation(item)) {
        if (nodeFrom && this.getNodeInformation(nodeFrom).preorder >= this.getNodeInformation(item).preorder) {
          visibleObjects.push(item);
        } else {
          this.getNodeInformationById(item[this.service.getIdField()]).visible = false;
        }
      }
    });
    this.visibleLimit = visibleObjects.length + this.visibleSize;
    this.allNode.forEach((item: HelisaNode<T>): void => {
      if (visibleObjects.length < this.visibleLimit &&
        (!nodeFrom || this.getNodeInformation(nodeFrom).preorder < item.preorder)) {
        const idParent: string = item.object[this.service.getIdParentField()];
        if (!idParent) {
          visibleObjects.push(item.object);
          item.visible = true;
        } else {
          const parentInformation: HelisaNode<T> = this.getNodeInformationById(idParent);
          if (parentInformation.visible && parentInformation.expanded) {
            visibleObjects.push(item.object);
            item.visible = true;
          }
        }
      }
    });
    this.visibleObjects = visibleObjects;
  }

  collapseNode(item: T): void {
    this.getNodeInformationById(item[this.service.getIdField()]).expanded = false;
    this.loadNextVisibleObjects(item);
  }

  expandNode(item: T): void {
    this.getNodeInformationById(item[this.service.getIdField()]).expanded = true;
    this.loadNextVisibleObjects(item);
  }

  showNextPage(): void {
    if (this.visibleObjects.length > 0) {
      this.loadNextVisibleObjects(this.visibleObjects[this.visibleObjects.length - 1]);
    }
  }

  get visibleData(): ReadonlyArray<T> {
    return this.visibleObjects;
  }

  removeItem(item: T): void {
    this.removeById(item[this.service.getIdField()]);
  }

  removeById(id: string): void {
    if (this.getNodeInformationById(id)) {
      const idParent: string = this.getNodeInformationById(id).object[this.service.getIdParentField()];
      const set: Set<string> = new Set<string>();
      set.add(id);
      const beginIndex: number = this.allNode.findIndex(
        (itemSearch: HelisaNode<T>): boolean => itemSearch.object[this.service.getIdField()] === id
      );
      let lastIndex: number = this.allNode.length;
      for (let i: number = beginIndex + 1; i < this.allNode.length; i++) {
        const itemSearch: T = this.allNode[i].object;
        if (set.has(itemSearch[this.service.getIdParentField()])) {
          set.add(itemSearch[this.service.getIdField()]);
        } else {
          lastIndex = i;
          break;
        }
      }
      const deletedItems: HelisaNode<T>[] = this.allNode.splice(beginIndex, lastIndex - beginIndex);
      let parentHaveChildren: boolean = false;
      deletedItems.forEach((deletedItem: HelisaNode<T>): boolean => this.searchNode.delete(deletedItem.object[this.service.getIdField()]));
      this.allNode.forEach((searchItem: HelisaNode<T>, index: number): void => {
        searchItem.preorder = index + 1;
        if (searchItem.object[this.service.getIdParentField()] === idParent) {
          parentHaveChildren = true;
        }
      });
      if (idParent) {
        this.getNodeInformationById(idParent).haveChildren = parentHaveChildren;
      }
      this.loadNextVisibleObjects(beginIndex > 0 ? this.allNode[beginIndex - 1].object : null);
    }
  }

  addItem(item: T): void {
    const indexParent: number = this.allNode.findIndex((node: HelisaNode<T>): boolean => node.object[this.service.getIdField()] === item[this.service.getIdParentField()]);
    if (indexParent >= 0) {
      this.allNode.push(this.createNode(item));
      this.allNode[indexParent].haveChildren = true;
      this.reSort();
      this.expandNode(this.allNode[indexParent].object);
    } else {
      throw Error('No existe el padre.');
    }
  }

  updateItem(item: T): void {
    if (this.getNodeInformation(item)) {
      this.getNodeInformation(item).object = item;
      this.reSort();
      const indexParent: number = this.allNode.findIndex(
        (node: HelisaNode<T>): boolean => node.object[this.service.getIdField()] === item[this.service.getIdParentField()]);
      if (indexParent >= 0) {
        this.expandNode(this.allNode[indexParent].object);
      } else {
        this.loadNextVisibleObjects(null);
      }
    }
  }

  private reSort(): void {
    const items: T[] = this.allNode.map((node: HelisaNode<T>): T => node.object);
    items.sort((a: T, b: T): number => this.service.compare(a, b));
    const preorder: T[] = this.sortItems(items);
    preorder.forEach((object: T, index: number): number => this.getNodeInformation(object).preorder = index + 1);
    this.allNode.sort((nodeA: HelisaNode<T>, nodeB: HelisaNode<T>): number => nodeA.preorder - nodeB.preorder);
  }
}
