import {AfterViewInit, Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {ComboBoxListable} from './interface/combo-box-listable';
import {ComboBoxEditable} from './interface/combo-box-editable';

export enum ComboBoxHelisaState {
  CLOSED,
  SELECT,
  INSERT
}

@Component({
  selector: 'lib-combo-box-helisa',
  templateUrl: './combo-box-helisa.component.html',
  styleUrls: ['./combo-box-helisa.component.css']
})
export class ComboBoxHelisaComponent<TYPE> implements OnInit, AfterViewInit {

  @Input() editable: ComboBoxEditable<TYPE>;
  @Input() listable: ComboBoxListable<TYPE>;
  @Input() placeholder: string = 'Sin seleccionar';
  @Input() selectedItem: TYPE;
  @Output() selectEmitter: EventEmitter<TYPE> = new EventEmitter<TYPE>();
  @Input() enabled: boolean = true;

  private page: number = 0;
  private pageSize: number = 50;
  private haveNextPage: boolean = true;
  state: ComboBoxHelisaState = ComboBoxHelisaState.CLOSED;

  rows: TYPE[] = [];

  constructor() {
  }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.getNextPage();
  }

  private getNextPage(): void {
    if (this.haveNextPage) {
      this.listable.getData(this.page++, this.pageSize).subscribe((rows: TYPE[]): void => {
        rows.forEach((item: TYPE): number => this.rows.push(item));
        this.haveNextPage = rows.length > 0;
      });
    }
  }

  get comboBoxHelisaState(): typeof ComboBoxHelisaState {
    return ComboBoxHelisaState;
  }

  onFocus(): void {
    if (this.enabled) {
      this.state = ComboBoxHelisaState.SELECT;
    }
  }

  selectItem(row: TYPE): void {
    this.selectedItem = row;
    this.selectEmitter.emit(row);
    this.state = ComboBoxHelisaState.CLOSED;
  }

  changeToInsert(): void {
    this.state = ComboBoxHelisaState.INSERT;
  }

  insert(event: string): void {
    if (event.trim().length > 0) {
      this.editable.insert(event).subscribe(
        (data: TYPE): void => {
          this.rows.push(data);
          this.state = ComboBoxHelisaState.SELECT;
        }
      );
    } else {
      this.state = ComboBoxHelisaState.SELECT;
    }
  }

  onScroll(event: Event): void {
    const element: HTMLDivElement = event.target as HTMLDivElement;
    if (element.scrollHeight - element.scrollTop < 1000) {
      this.getNextPage();
    }
  }
}
