import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { FormControl } from '@angular/forms';

export enum InputHelisaType {
  DEFAULT, IDENTITY, NUMERIC, DOUBLE, POSITIVEORNEGATIVEDOUBLE, PHONE
}

@Component({
  selector: 'hel-input',
  templateUrl: './input-helisa.component.html',
  styleUrls: ['./input-helisa.component.sass']
})
export class InputHelisaComponent implements OnInit, AfterViewInit {

  private readonly DECIMAL_SEPARATOR: string = '.';
  private readonly THOUSAND_SEPARATOR: string = ',';
  private readonly NEGATIVE_SIGN: string = '-';

  @Input() placeholder: string = '';
  @Input() floatLabel: 'never' | 'always' | 'auto' = 'never';
  @Input() minlength: number;
  @Input() maxlength: number;

  /** Activar o desactivar el autocompletado
   * (Caracteristica de los navegadores para campos comunes como
   * Direccion , Usuario, Password ... etc)
   */
  @Input() autocompleteMode: boolean = false;

  // Mostrar o no el icono de buscar
  @Input() isSearch: boolean = false;
  // @Input() inputFormControl: FormControl = new FormControl('');
  @Input() isFocused: boolean = false;

  /**
   * Deprecated
   */
  @Input() disabled: boolean = false;
  @Input() type: InputHelisaType = InputHelisaType.DEFAULT;

  /**
   * Deprecated
   */
  @Output() setValue: EventEmitter<string> = new EventEmitter<string>();

  // tslint:disable-next-line:no-any
  @Output() blur: EventEmitter<any> = new EventEmitter();

  formControlMask: FormControl = new FormControl('');
  private realValue: string = '';
  private inputFormReal: FormControl = new FormControl('');

  @ViewChild('inputText', {static: true}) inputText: ElementRef;

  constructor() {
  }

  @Input()
  set inputFormControl(formControl: FormControl) {
    this.inputFormReal = formControl;
    this.inputFormReal.registerOnDisabledChange(((isDisabled: boolean): void => {
      if (isDisabled) {
        this.formControlMask.disable();
      } else {
        this.formControlMask.enable();
      }
    }));
    this.inputFormReal.valueChanges.subscribe((data: string): void => {
      this.statusChange(this.inputFormReal.status);
      if (this.getMaskedValue(data) !== this.formControlMask.value) {
        this.change(data);
        if (this.isFocused) {
          this.onFocus(null);
        }
      }
    });
    this.formControlMask.setValidators(this.inputFormReal.validator);
    this.change(this.inputFormReal.value);
    // disable control
    if (formControl.disabled) {
      this.formControlMask.disable({ onlySelf: true });
    }
    this.inputFormReal.statusChanges.subscribe(
      (data: string): void => {
        this.statusChange(data);
        if (this.isFocused) {
          this.onFocus(null);
        }
      }
    );
  }


  private statusChange(data: string): void {
    if (data === 'INVALID') {
      this.formControlMask.setErrors({ key: 'Error de validacin.' });
      this.formControlMask.markAsTouched();
    } else {
      this.formControlMask.setErrors(null);
    }
  }

  ngOnInit(): void {
    if (this.isFocused) {
      this.inputText.nativeElement.focus();
    }
  }

  ngAfterViewInit(): void {
    // this.isParentDisabled();
  }

  /*isParentDisabled(): void {
    setTimeout(() => {
      if (this.nameInput.nativeElement.closest('.hw-disabled-mode')) {
        this.disabled = true;
      } else {
        this.disabled = false;
      }
    });
  }*/

  search(): void {
    this.setValue.emit(this.realValue);
  }

  change(event: string): void {
    if (event != null) {
      event = event + '';
    }
    let position: number = this.inputText.nativeElement.selectionStart;
    const length: number = event ? event.length : 0;
    this.realValue = this.getRealValue(event);
    if (this.getMaskedValue(this.realValue) !== this.formControlMask.value) {
      this.formControlMask.setValue(this.getMaskedValue(this.realValue));
      position += this.inputText.nativeElement.value.length - length;
      this.inputText.nativeElement.selectionStart = position;
      this.inputText.nativeElement.selectionEnd = position;
    }
    this.inputFormReal.setValue(this.realValue);
  }

  private getMaskedValue(str: string): string {
    if (str == null) {
      return str;
    }
    str = str + '';
    if (this.type === InputHelisaType.DEFAULT || this.type === InputHelisaType.PHONE) {
      return str;
    }
    let maskedStr: string = '';
    if (this.type === InputHelisaType.IDENTITY) {
      for (let i: number = str.length - 1, j: number = 0; i >= 0; i--, j++) {
        if (j > 0 && j % 3 === 0) {
          maskedStr = this.DECIMAL_SEPARATOR + maskedStr;
        }
        maskedStr = str[i] + maskedStr;
      }
    }
    if (this.type === InputHelisaType.NUMERIC) {
      for (let i: number = str.length - 1, j: number = 0; i >= 0; i--, j++) {
        if (j > 0 && j % 3 === 0) {
          maskedStr = this.THOUSAND_SEPARATOR + maskedStr;
        }
        maskedStr = str[i] + maskedStr;
      }
    }
    if (this.type === InputHelisaType.DOUBLE) {
      maskedStr = this.getMaskedValueDouble(str);
    }
    if (this.type === InputHelisaType.POSITIVEORNEGATIVEDOUBLE) {
      const isNegativeValue: boolean = str.indexOf(this.NEGATIVE_SIGN) === 0;
      const newStr: string = isNegativeValue ? str.replace(this.NEGATIVE_SIGN, '') : str;
      maskedStr = this.getMaskedValueDouble(newStr);
      if (isNegativeValue) {
        maskedStr = this.NEGATIVE_SIGN + maskedStr;
      }
    }
    return maskedStr;
  }

  private getMaskedValueDouble(str: string): string {
    let maskedStr: string = '';
    if (str.indexOf(this.DECIMAL_SEPARATOR) >= 0) {
      for (let i: number = str.indexOf(this.DECIMAL_SEPARATOR); i < str.length; i++) {
        maskedStr += str[i];
      }
    }
    for (let i: number = (str.indexOf(this.DECIMAL_SEPARATOR) >= 0 ? str.indexOf(this.DECIMAL_SEPARATOR) : str.length) - 1,
      j: number = 0; i >= 0; i--, j++) {

      if (j > 0 && j % 3 === 0) {
        maskedStr = this.THOUSAND_SEPARATOR + maskedStr;
      }
      maskedStr = str[i] + maskedStr;
    }
    return maskedStr;
  }

  private getRealValue(str: string): string {
    if (str == null) {
      return str;
    }
    str = str + '';
    let realStr: string = '';
    if (this.type === InputHelisaType.DEFAULT) {
      return str;
    }
    if (this.type === InputHelisaType.IDENTITY || this.type === InputHelisaType.NUMERIC || this.type === InputHelisaType.PHONE) {
      for (const strItem of str) {
        if (strItem.match('[0-9]')) {
          realStr += strItem;
        }
      }
    }
    if (this.type === InputHelisaType.DOUBLE) {
      realStr = this.getRealValueDouble(str);
    }
    if (this.type === InputHelisaType.POSITIVEORNEGATIVEDOUBLE) {
      const isNegativeValue: boolean = str.indexOf(this.NEGATIVE_SIGN) === 0;
      const newStr: string = isNegativeValue ? str.replace(this.NEGATIVE_SIGN, '') : str;
      realStr = this.getRealValueDouble(newStr)
      if (isNegativeValue) {
        realStr = this.NEGATIVE_SIGN + realStr;
      } 
    }
    return realStr;
  }

  getRealValueDouble(str: string): string {
    let realStr: string = '';
    let haveDot: boolean = false;

      for (const strItem of str) {
        if (strItem.match('[0-9]') || ((strItem === this.DECIMAL_SEPARATOR) && !haveDot)) {
          realStr += strItem;
        }
        haveDot = haveDot || (strItem === this.DECIMAL_SEPARATOR);
      }
    return realStr;
  }

  onFocus($event: FocusEvent): void {
    if ((this.type === InputHelisaType.NUMERIC || this.type === InputHelisaType.DOUBLE || this.type === InputHelisaType.POSITIVEORNEGATIVEDOUBLE) &&
      Number(this.getRealValue(this.inputText.nativeElement.value)) === 0) {
      this.inputText.nativeElement.select();
    }
  }
}
